/* eslint-disable multiline-ternary */
import {
  Fragment,
  useContext,
  useEffect,
  useState,
  useCallback,
  useRef,
  createRef
} from "react"
import { useParams } from "react-router-dom"
import toast from "react-hot-toast"
import Cleave from "cleave.js/react"
import {
  Row,
  Col,
  Form,
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  CardText,
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  ListGroup,
  ListGroupItem
} from "reactstrap"
import { useDropzone } from "react-dropzone"
import { FileText, X, DownloadCloud } from "react-feather"
import { useTranslation } from "react-i18next"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import "moment/locale/id"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { selectThemeColors, sortedObject } from "@utils"
import Select, { components } from "react-select"
import Flatpickr from "react-flatpickr"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import { Indonesian } from "flatpickr/dist/l10n/id.js"
import { useForm, Controller } from "react-hook-form"
import {
  storeName,
  pageTitle,
  postData,
  putData,
  resetSelectedData,
  getDataById
} from "../store"

import { useDispatch, useSelector } from "react-redux"
import ApiIcon from "@mui/icons-material/Api"
import WebIcon from "@mui/icons-material/Web"
import InstallMobileIcon from "@mui/icons-material/InstallMobile"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"
import classnames from "classnames"
import { getOrientation } from "get-orientation/browser"
import {
  getCroppedImg,
  getRotatedImage
} from "../../../../utility/canvasUtils"
import Cropper from "react-easy-crop"
import Slider from "@mui/material/Slider"
const ORIENTATION_TO_ANGLE = {
  3: 180,
  6: 90,
  8: -90
}

const ModalAddEdit = ({
  show,
  setShow,
  detailPage,
  formState,
  setFormState
}) => {
  const { t } = useTranslation()
  const { id } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const { colors } = useContext(ThemeColors)
  const [codeApp, setCodeApp] = useState("")
  const [appType, setAppType] = useState("")
  const [mainLanguage, setMainLanguage] = useState("")
  const [framework, setFramework] = useState("")
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [imageSrc, setImageSrc] = useState(null)
  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [rotation, setRotation] = useState(0)
  const [zoom, setZoom] = useState(1)
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null)
  // const [fileToUpload, setFileToUpload] = useState()

  const {
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({})

  const { Option } = components
  const IconOption = (props) => (
    <Option {...props}>
      {props.data.icon}
      {props.data.label}
    </Option>
  )

  const appTypeOptions = [
    {
      value: "service",
      label: "Service",
      icon: <ApiIcon className="me-1" sixe={32} />
    },
    {
      value: "web",
      label: "Web App",
      icon: <WebIcon className="me-1" sixe={32} />
    },
    {
      value: "mobile",
      label: "Mobile App",
      icon: <InstallMobileIcon className="me-1" sixe={32} />
    }
  ]

  const IconImgOption = (props) => (
    <Option {...props}>
      <img
        className="me-1"
        src={props.data.icon}
        style={{ width: 36 }}
        alt={props.data.label}
      />
      {props.data.label}
    </Option>
  )

  const appMainLanguageOptions = [
    {
      value: "python",
      label: "Python",
      icon: "https://logos-download.com/wp-content/uploads/2016/10/Python_logo_wordmark.png"
    },
    {
      value: "javascript",
      label: "Javascript",
      icon: "https://javascriptsu.files.wordpress.com/2020/12/js.jpg"
    }
  ]

  const appFrameworkOptions = [
    {
      value: "react",
      label: "React",
      icon: "https://logos-download.com/wp-content/uploads/2016/09/React_logo_wordmark.png"
    },
    {
      value: "angular",
      label: "Angular",
      icon: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSQgREvN-e1uVZXVDc2w_zLeey0Li4buQ26gF0ZmCYxaBMoybo4uQTncS3Z5WxHRyZNfA&usqp=CAU"
    },
    {
      value: "flask",
      label: "Flask",
      icon: "https://miro.medium.com/max/1079/1*nvtdCw77Al6LgV8wt_ma6Q.png"
    },
    {
      value: "flutter",
      label: "Flutter",
      icon: "https://png.pngitem.com/pimgs/s/480-4803274_google-flutter-hd-png-download.png"
    }
  ]

  const countChanges = (data) => {
    if (formState === "edit") {
      const storeData = { ...sortedObject(store.selectedData) }
      delete storeData["id"]
      for (let key in storeData) {
        if (!(key in data)) delete storeData[key]
      }
      // console.log(JSON.stringify(sortedObject(storeData)))
      // console.log(JSON.stringify(sortedObject(data)))
      return (
        JSON.stringify(sortedObject(storeData)) !==
        JSON.stringify(sortedObject(data))
      )
    } else {
      return true
    }
  }

  const handleInputName = (event) => {
    const value = event.target.value
    const newValue = value
      .replace(/[^a-zA-Z0-9 ]/g, "")
      .replaceAll(" ", "_")
      .toLowerCase()
    setCodeApp(newValue)
  }

  const updateDataToApi = async (data) => {
    if (countChanges(data)) {
      if (
        Object.values(data).every((field) => {
          return field !== null && field !== ""
        })
      ) {
        // console.log(rotation)
        // console.log(crop)
        if (
          imageSrc.includes("data:image/png;base64") ||
          crop !== 0 ||
          rotation !== { x: 0, y: 0 }
        ) {
          const croppedImage = await getCroppedImg(
            imageSrc,
            croppedAreaPixels,
            rotation
          )
          data["logo"] = { appLogo: croppedImage }
        }
        // console.log(data)
        setLoadingText(`${t("Updating")} ${t(pageTitle)} ${t("Detail")} Data`)
        dispatch(putData(data)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            setShow(!show)
            toast.success(`${t("Successfully Updated")} ${t(pageTitle)}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
            if (detailPage) {
              dispatch(getDataById(id))
            }
          } else if (res.meta.requestStatus === "rejected") {
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      } else {
        for (const key in data) {
          if (data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      }
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "ðŸ‘" })
    }
  }

  const postDataToApi = async (formData) => {
    const data = {}
    data["name"] = formData["name"]
    data["code"] = codeApp
    data["info"] = {
      description: formData["description"],
      appType: appType,
      programming_language: {
        main_language: mainLanguage,
        language_others: [],
        framework: framework,
        framework_ver: null,
        dependencies: []
      }
    }
    data["logo"] = {
      appLogo:
        "https://res.cloudinary.com/insaba/image/upload/v1661262584/app/insaba_starterkit_services/logo.png",
      favicon:
        "https://res.cloudinary.com/insaba/image/upload/v1661262584/app/insaba_starterkit_services/favicon.png"
    }

    if (imageSrc) {
      const croppedImage = await getCroppedImg(
        imageSrc,
        croppedAreaPixels,
        rotation
      )
      console.log("donee", { croppedImage })
      data["logo"]["appLogo"] = croppedImage
    }
    // console.log(data)

    if (
      Object.values(data).every((field) => {
        return field !== null && field !== ""
      })
    ) {
      setLoadingText(`${t(`Creating a New ${pageTitle}`)}`)
      dispatch(postData(data)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setShow(!show)
          toast.success(`${t("Successfully Created")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
          if (detailPage) {
            dispatch(getDataById(id))
          }
        } else if (res.meta.requestStatus === "rejected") {
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      for (const key in data) {
        if (data[key] !== null && data[key]?.length === 0) {
          setError(key, {
            type: "manual"
          })
        }
      }
    }
  }

  const handleReset = () => {
    dispatch(resetSelectedData())
  }

  // ** UPLOAD
  const { getRootProps, getInputProps } = useDropzone({
    multiple: false,
    onDrop: (acceptedFiles) => {
      const file = acceptedFiles[0]
      onFileChange(file)
    }
  })

  const handleRemoveAllFiles = (e) => {
    setImageSrc(null)
  }

  //////CROP

  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels)
  }, [])

  function readFile(file) {
    return new Promise((resolve) => {
      const reader = new FileReader()
      reader.addEventListener("load", () => resolve(reader.result), false)
      reader.readAsDataURL(file)
    })
  }

  const onFileChange = async (file) => {
    let imageDataUrl = await readFile(file)
    const orientation = await getOrientation(file)
    const rotation = ORIENTATION_TO_ANGLE[orientation]
    if (rotation) {
      imageDataUrl = await getRotatedImage(imageDataUrl, rotation)
    }

    setImageSrc(imageDataUrl)
  }

  useEffect(() => {
    if (formState === "edit") {
      setImageSrc(store.selectedData.logo.appLogo ?? null)
    }
  }, [])

  return (
    <Modal
      isOpen={show}
      toggle={() => {
        handleReset()
        setShow(!show)
      }}
      className="modal-dialog-centered modal-lg"
    >
      <UILoader
        blocking={store.isLoadingAddEdit || store.isLoadingDetail}
        loader={<Loader />}
      >
        <ModalHeader
          className="bg-transparent"
          toggle={() => {
            handleReset()
            setShow(!show)
          }}
        ></ModalHeader>
        <ModalBody className="px-sm-5 pt-50 pb-5">
          <div className="text-center mb-2">
            <h2 className="mb-1">
              {formState === "edit" ? t("Edit") : t("Add")} {t(pageTitle)}
            </h2>
          </div>
          <Form
            onSubmit={
              formState === "edit"
                ? handleSubmit(updateDataToApi)
                : handleSubmit(postDataToApi)
            }
          >
            <Row className="gy-1 pt-75">
              <Col md={8} xs={12}>
                <Row>
                  <Col md={12} xs={12} className="mb-1">
                    <Label className="form-label" for="name">
                      {t("App Name")}
                      <span className="text-danger">*</span>
                    </Label>
                    <Controller
                      defaultValue={store.selectedData?.name ?? ""}
                      control={control}
                      id="name"
                      name="name"
                      render={({ field }) => (
                        <Input
                          {...field}
                          className={
                            errors.name
                              ? "form-control is-invalid"
                              : "form-control"
                          }
                          onInput={handleInputName}
                          id="name"
                          invalid={errors.name && true}
                        />
                      )}
                    />
                  </Col>

                  <Col md={12} xs={12} className="mb-1">
                    <Label className="form-label" for="code">
                      {t("Code")}
                      <span className="text-danger">*</span>
                    </Label>
                    <Controller
                      defaultValue={store.selectedData?.code ?? codeApp}
                      control={control}
                      id="code"
                      name="code"
                      render={({ field }) => (
                        <Input
                          disabled={true}
                          readOnly={true}
                          {...field}
                          value={store.selectedData?.code ?? codeApp}
                          className={
                            errors.code
                              ? "form-control is-invalid"
                              : "form-control"
                          }
                          id="code"
                          invalid={errors.code && true}
                        />
                      )}
                    />
                  </Col>

                  <Col md={12} xs={12} className="mb-1">
                    <Label className="form-label" for="appType">
                      {t("App Type")}
                    </Label>
                    <Select
                      id="appType"
                      theme={selectThemeColors}
                      defaultValue={appTypeOptions.filter(
                        (el) => el.value === store.selectedData?.info?.appType
                      )}
                      onChange={(data) => {
                        setAppType(data.value)
                      }}
                      options={appTypeOptions}
                      components={{ Option: IconOption }}
                      className={classnames("react-select", {
                        "is-invalid": appType === null
                      })}
                      classNamePrefix="select"
                    />
                  </Col>
                </Row>
              </Col>

              <Col md={4} xs={12}>
                {/* <Label className="form-label" for="name">
                  {t("App Logo")}
                  <span className="text-danger">*</span>
                </Label> */}

                <CardBody
                  style={{ padding: 0, margin: "auto" }}
                  className="text-center"
                >
                  {imageSrc && (
                    <div className="crop-container2">
                      <Cropper
                        image={imageSrc}
                        crop={crop}
                        rotation={rotation}
                        zoom={zoom}
                        aspect={1}
                        showGrid={false}
                        onCropChange={setCrop}
                        onRotationChange={setRotation}
                        onCropComplete={onCropComplete}
                        onZoomChange={setZoom}
                        restrictPosition={false}
                        // cropSize={{ width: "110px", height: "110px" }}
                        objectFit="vertical-cover"
                      />
                    </div>
                  )}
                  {!imageSrc ? (
                    <div {...getRootProps({ className: "dropzone" })}>
                      <input {...getInputProps()} />
                      <div className="d-flex align-items-center justify-content-center flex-column">
                        <DownloadCloud size={64} />
                        <h5>{t("Drop Files here or click to upload")}</h5>
                        <p className="text-secondary">
                          {t("Drop files here or click")}{" "}
                          <a href="/" onClick={(e) => e.preventDefault()}>
                            {t("browse")}
                          </a>{" "}
                          {t("through your machine")}
                        </p>
                      </div>
                    </div>
                  ) : null}
                  {imageSrc ? (
                    <Fragment>
                      <div className="d-flex justify-content-left align-items-center">
                        <div
                          style={{ width: "95px" }}
                          className="d-flex flex-column me-1"
                        >
                          <span>{t("Zoom")}</span>
                        </div>
                        <Slider
                          value={zoom}
                          min={1}
                          max={3}
                          step={0.1}
                          aria-labelledby="Zoom"
                          onChange={(e, zoom) => setZoom(zoom)}
                        />
                      </div>

                      <div className="d-flex justify-content-left align-items-center  mb-1">
                        <div
                          style={{ width: "95px" }}
                          className="d-flex flex-column me-1"
                        >
                          <span>{t("Rotation")}</span>
                        </div>
                        <Slider
                          value={rotation}
                          min={0}
                          max={360}
                          step={1}
                          aria-labelledby="Rotation"
                          onChange={(e, rotation) => setRotation(rotation)}
                        />
                      </div>

                      {/* <ListGroup className="my-2">{fileList}</ListGroup> */}
                      <div className="d-flex justify-content-center">
                        <Button
                          color="danger"
                          outline
                          onClick={handleRemoveAllFiles}
                        >
                          {t("Change")}
                        </Button>
                      </div>
                    </Fragment>
                  ) : null}
                </CardBody>
              </Col>

              <Col md={12} xs={12}>
                <Label className="form-label" for="main_language">
                  {t("Programming Language")}
                </Label>
                <Select
                  id="main_language"
                  theme={selectThemeColors}
                  defaultValue={appMainLanguageOptions.filter(
                    (el) =>
                      el.value ===
                      store.selectedData?.info?.programming_language
                        ?.main_language
                  )}
                  onChange={(data) => {
                    setMainLanguage(data.value)
                  }}
                  options={appMainLanguageOptions}
                  components={{ Option: IconImgOption }}
                  className="react-select"
                  classNamePrefix="select"
                />
              </Col>

              <Col md={12} xs={12}>
                <Label className="form-label" for="framework">
                  {t("Framework")}
                </Label>
                <Select
                  id="framework"
                  theme={selectThemeColors}
                  defaultValue={appFrameworkOptions.filter(
                    (el) =>
                      el.value ===
                      store.selectedData?.info?.programming_language?.framework
                  )}
                  onChange={(data) => {
                    setFramework(data.value)
                  }}
                  options={appFrameworkOptions}
                  components={{ Option: IconImgOption }}
                  className="react-select"
                  classNamePrefix="select"
                />
              </Col>

              <Col md={12} xs={12}>
                <Label className="form-label" for="description">
                  {t("Description")}
                  <span className="text-danger"></span>
                </Label>
                <Controller
                  defaultValue={store.selectedData?.name ?? ""}
                  control={control}
                  id="description"
                  name="description"
                  render={({ field }) => (
                    <Input
                      {...field}
                      type="textarea"
                      className="form-control"
                      id="description"
                    />
                  )}
                />
              </Col>

              <Col xs={12} className="text-center mt-2 pt-50">
                <Button type="submit" className="me-1" color="primary">
                  {t("Submit")}
                </Button>
                <Button
                  type="reset"
                  color="secondary"
                  outline
                  onClick={() => {
                    handleReset()
                    setFormState("add")
                    setShow(false)
                  }}
                >
                  {t("Cancel")}
                </Button>
              </Col>
            </Row>
          </Form>
        </ModalBody>
      </UILoader>
    </Modal>
  )
}

export default ModalAddEdit
